import React from 'react';
import { BsFillXCircleFill, BsBook } from 'react-icons/bs';
import { HiDownload } from 'react-icons/hi';
import { ImEye } from 'react-icons/im';
import { TbLanguage } from 'react-icons/tb';
import { Row, Col } from 'react-bootstrap';

const Modal = ({ show, item, onClose }) => {
  if (!show) {
    return null;
  }
  let thumbnail =
    item.volumeInfo.imageLinks && item.volumeInfo.imageLinks.smallThumbnail;
  let epb = item.accessInfo.epub.acsTokenLink;
  let pdf = item.accessInfo.pdf.acsTokenLink;
  return (
    <>
      <div className="overlay">
        <div className="overlay-inner scroll">
          <button className="close" onClick={onClose}>
            <BsFillXCircleFill className="fs-3 me-3 mt-3 fixed" />
          </button>
          <div className="inner-box">
            <Row>
              <Col lg={6} md={6} sm={12}>
                <img src={thumbnail} alt="Cover Img" />
              </Col>
              <Col className="me-5">
                <div className="ms-5 info text-center">
                  <h1>{item.volumeInfo.title}</h1>
                  <h3>{item.volumeInfo.authors}</h3>
                  <h4>
                    Page Count:
                    <span className="mt-0">{item.volumeInfo.pageCount}</span>
                  </h4>
                  <h4>
                    <BsBook className="fs-3 fw-bold " />
                    &nbsp; Publisher: &nbsp;
                    <span> {item.volumeInfo.publisher}</span>
                  </h4>
                  <h4>
                    <TbLanguage className="fs-3 fw-bold " />
                    &nbsp;
                    <span>Language: {item.volumeInfo.language}</span>
                  </h4>
                  <br />
                  <a
                    className="mx-3 btn btn-secondary fs-5 fw-bold my-3"
                    href={item.volumeInfo.previewLink}
                  >
                    <ImEye className="fs-3 fw-bold" /> PREVIEW
                  </a>
                  <a
                    className="mx-3 btn btn-warning fs-5 fw-bold my-3"
                    href={epb}
                  >
                    <HiDownload className="fs-3 fw-bold" /> EPUB
                  </a>
                  <a
                    className="btn btn-danger mx-3 fs-5 fw-bold my-3"
                    href={pdf}
                  >
                    <HiDownload className="fs-3 fw-bold" /> PDF
                  </a>
                </div>
              </Col>
            </Row>
          </div>
          <h4 className="description">{item.volumeInfo.description}</h4>
        </div>
      </div>
    </>
  );
};
export default Modal;
